<template>
  <div>
    <el-form :inline="true" :model="searchForm">
      <el-form-item label="搜索内容">
        <el-input v-model="searchForm.text" placeholder="请输入搜索内容"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="SearchButtonClick">搜索</el-button>
        <el-button v-if="isAdmin" type="danger" :disabled="deleteButtonDisable" @click="DeleteUserClick()">删除选中项</el-button>
      </el-form-item>
    </el-form>

    <!--    table-->
    <el-table
      ref="multipleTable"
      :data="tableData"
      height="300px"
      style="width: 100%"
      @selection-change="handleSelectionChange">

      <el-table-column
        type="selection"
        width="55">
      </el-table-column>

      <el-table-column label="ID" width="180">
        <template slot-scope="scope">
          <span>{{ scope.row.id}}</span>
        </template>
      </el-table-column>

      <el-table-column label="文件标题" width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.title}}</span>
        </template>
      </el-table-column>

      <el-table-column label="文件详情" width="250">
        <template slot-scope="scope">
          <span>{{ scope.row.detail}}</span>
        </template>
      </el-table-column>

      <el-table-column label="创建时间" width="130">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{ scope.row.createDate}}</span>
        </template>
      </el-table-column>

      <el-table-column label="创建人" width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.creator}}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button v-if="isAdmin"
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            type="primary"
            size="mini"
            @click="handleDownload(scope.row)">下载</el-button>
        </template>
      </el-table-column>

    </el-table>


    <el-row type="flex" justify="center">
      <el-col :span="20">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 25]"
          :page-size="currentPageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalPage">
        </el-pagination>
      </el-col>
    </el-row>

    <el-dialog title="文件信息修改" :visible.sync="editFormVisible">
      <el-form label-width="150px" :model="editForm" :rules="editRules" ref="editForm">
        <el-form-item label="文件名称" prop="name">
          <el-input style="max-width: 300px" v-model="editForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="文件详情" prop="detail">
          <el-input style="max-width: 300px" v-model="editForm.detail" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="选取文件">
          <el-upload style="margin-bottom: 10px"
                     :limit="1"
                     ref="upload"
                     action="http://127.0.0.1:2333/api/file/"
                     :data="editForm"
                     :on-preview="handlePreview"
                     :on-remove="handleRemove"
                     :on-error="handleError"
                     :on-success="handleSuccess"
                     :file-list="fileList"
                     :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="EditUser('editForm')">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import {GetFile,UpdateFile,DeleteFile} from "../api/hrmFile";

export default {
        name: "HrmFile",
        data() {
            return {
                fileList: [],
                isAdmin:false,
                searchForm: {
                    text: ''
                },
                isSearch:false,
                tableData: null,
                multipleSelection: [],
                totalPage:0,
                currentPage:1,
                currentPageSize:5,
                deleteButtonDisable:true,
                editFormVisible: false,
                editForm: {
                    title:'',
                    detail:''

                },
                editRules:{
                    title:[
                        { required: true, message: '请输入公告名称', trigger: 'blur' },
                    ],
                    detail:[
                        { required: true, message: '请输入公告内容', trigger: 'blur' },
                    ]
                }
            }
        },
        methods:{
            UpdateTableData(){
                let that = this
                let temp
                console.log(this.isSearch)
                if (that.isSearch) {
                    temp = {
                        'offset': this.currentPageSize * (this.currentPage - 1),
                        'limit': this.currentPageSize,
                        'searchText': this.searchForm.text,
                    }
                    console.log(this.searchForm.text)
                } else {
                    temp = {
                        'offset': this.currentPageSize * (this.currentPage - 1),
                        'limit': this.currentPageSize
                    }
                }
                GetFile(temp).then(function (e) {
                    console.log(e)
                    if(e.status=='ok'){
                        if(e.data.datas.length){
                            that.tableData = e.data.datas
                            that.totalPage=e.data.total
                        }else {
                            that.$message({
                                message: "暂无匹配数据",
                                type: 'info'
                            })
                        }
                    }else {
                        that.$message({
                            message:e.data,
                            type:'error'
                        })
                    }
                }).catch(function (e) {
                    console.log(e)
                })
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handleSuccess(e) {
                let that=this
                if (e.status == 'ok') {
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    })
                    that.UpdateTableData()
                    that.editFormVisible=false
                } else {
                    this.$message({
                        message: e.data,
                        type: 'error'
                    })
                }
            },
            handleError(e){
                console.log(e)
            },
            handlePreview(file) {
                console.log(file);
            },
            handleDownload(row){
                window.location.href="http://127.0.0.1:2333/"+row.url

            },
            handleEdit(index, row) {
                this.editForm=row
                this.editForm.sessionId=this.$store.state.sessionId
                this.editFormVisible=true
            },
            handleSizeChange(val) {
                this.currentPageSize=val
                this.currentPage=1
                this.UpdateTableData()
            },
            handleCurrentChange(val) {
                this.currentPage=val
                this.UpdateTableData()
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                if(this.multipleSelection.length==0){
                    this.deleteButtonDisable=true
                }else {
                    this.deleteButtonDisable=false
                }
            },
            SearchButtonClick(){
                if(this.searchForm.text){
                    this.isSearch=true
                    this.currentPage=1
                    console.log(this.searchForm.text)
                    this.UpdateTableData()
                }else {
                    this.isSearch=false
                    this.currentPage=1
                    this.UpdateTableData()
                }
            },
            EditUser(formName){
                let that=this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$refs.upload.submit();

                        // UpdateFile(that.editForm).then(function (e) {
                        //     console.log(that.editForm)
                        //     if(e.status=='ok'){
                        //         that.$message({
                        //             message:'修改成功',
                        //             type:'success'
                        //         })
                        //         that.UpdateTableData()
                        //         that.editFormVisible=false
                        //     }else {
                        //         that.$message({
                        //             message:e.data,
                        //             type:'error'
                        //         })
                        //     }
                        // }).catch(function (e) {
                        //     console.log(e)
                        // })
                    } else {
                        return false;
                    }
                });
            },
            DeleteUserClick(){
                let that=this
                let tempArray=new Array()
                for(var i=0;i<this.multipleSelection.length;i++){
                    tempArray[i]=this.multipleSelection[i].id
                }
                that.$alert('是否删除选中的条目', '删除提示', {
                    confirmButtonText: '确定',
                    callback: action => {
                        if(action=='confirm'){
                            DeleteFile(tempArray).then(function (e) {
                                if(e.status=='ok'){
                                    that.$message({
                                        message:'删除成功',
                                        type:'success'
                                    })
                                    that.UpdateTableData()
                                }else {
                                    that.$message({
                                        message:e.data,
                                        type:'error'
                                    })
                                }
                            }).catch(function (e) {
                                console.log(e)
                            })
                        }
                    }
                });
            }
        },
        mounted() {
            this.UpdateTableData()
        },
    updated() {
            this.isAdmin=this.$store.state.isAdmin
    }
}
</script>

<style scoped>

</style>



