import Vue from 'vue'
import Vuex from 'vuex'
import {GetSession,SetSession,RemoveSession} from "../util/auth";

Vue.use(Vuex)
const state={
  sessionId:GetSession(),
  isLogin:false,
  isAdmin:false,
  isLogOut:false,
  nickName:'',
  menuList:null
}

const mutations={
  SetHrmTabs:(state,title,name)=>{

  },
  SetSessionTemp:(state,sessionId)=>{
    state.sessionId=sessionId
    state.isLogOut=false
  },
  SetSession:(state,sessionId)=>{
    state.sessionId=sessionId
    SetSession(sessionId)
    state.isLogOut=false
  },
  LogOut:(state)=>{
    state.sessionId=null
    state.isLogOut=true
    RemoveSession()
  },
  SetUserInfo:(state,data)=>{
    if(data.type==1){
      state.isAdmin=true
      state.menuList=[
        {
          name:'用户管理',
          icon:'el-icon-user',
          subMenu:[
            {
              name:'用户查询',
              router:'/user'
            },
            {
              name:'添加用户',
              router:'/useredit'
            }
          ]
        },
        {
          name:'部门管理',
          icon:'el-icon-menu',
          subMenu:[
            {
              name:'部门查询',
              router:'/department'
            },
            {
              name:'添加部门',
              router:'/departmentedit'
            }
          ]
        },
        {
          name:'职位管理',
          icon:'el-icon-news',
          subMenu:[
            {
              name:'职位查询',
              router:'/position'
            },
            {
              name:'添加职位',
              router:'/positionedit'
            }
          ]
        },
        {
          name:'员工管理',
          icon:'el-icon-service',
          subMenu:[
            {
              name:'员工查询',
              router:'/staff'
            },
            {
              name:'添加员工',
              router:'/staffedit'
            }
          ]
        },
        {
          name:'公告管理',
          icon:'el-icon-message',
          subMenu:[
            {
              name:'公告查询',
              router:'/bulletin'
            },
            {
              name:'添加公告',
              router:'/bulletinedit'
            }
          ]
        },
        {
          name:'下载中心',
          icon:'el-icon-tickets',
          subMenu:[
            {
              name:'文档查询',
              router:'/file'
            },
            {
              name:'上传文档',
              router:'/fileedit'
            }
          ]
        }
      ]
    }else {
      state.isAdmin=false
      state.menuList=[
        {
          name:'用户管理',
          icon:'el-icon-user',
          subMenu:[
            {
              name:'用户查询',
              router:'/user'
            }
          ]
        },
        {
          name:'部门管理',
          icon:'el-icon-menu',
          subMenu:[
            {
              name:'部门查询',
              router:'/department'
            }
          ]
        },
        {
          name:'职位管理',
          icon:'el-icon-news',
          subMenu:[
            {
              name:'职位查询',
              router:'/position'
            }
          ]
        },
        {
          name:'员工管理',
          icon:'el-icon-service',
          subMenu:[
            {
              name:'员工查询',
              router:'/staff'
            }
          ]
        },
        {
          name:'公告管理',
          icon:'el-icon-message',
          subMenu:[
            {
              name:'公告查询',
              router:'/bulletin'
            }
          ]
        },
        {
          name:'下载中心',
          icon:'el-icon-tickets',
          subMenu:[
            {
              name:'文档查询',
              router:'/file'
            }
          ]
        }
      ]
    }
    state.nickName=data.nickName
  }
}

const actions={}

const getters={
  GetIsAdmin:state=>{
    return state.isAdmin
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



