<template>
  <div>
    <el-form style="width: 400px" :model="createForm" :rules="createFormRules" ref="createForm" label-width="100px">

      <el-form-item label="文件名称" prop="title">
        <el-input v-model="createForm.title"></el-input>
      </el-form-item>

      <el-form-item label="文件描述" prop="detail">
        <el-input v-model="createForm.detail"></el-input>
      </el-form-item>

      <el-form-item label="选取文件">
        <el-upload style="margin-bottom: 10px"
                   :limit="1"
                   ref="upload"
                   action="http://127.0.0.1:2333/api/file/"
                   :data="createForm"
                   :on-preview="handlePreview"
                   :on-remove="handleRemove"
                   :on-error="handleError"
                   :on-success="handleSuccess"
                   :file-list="fileList"
                   :auto-upload="false">
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>



      <el-form-item>
        <el-button type="primary" @click="CreatUser('createForm')">立即创建</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import {CreateFile} from "../api/hrmFile";

  export default {
        name: "HrmFileEdit",
        data(){
            return{
                createForm:{
                    title:'',
                    detail:'',
                    sessionId:this.$store.state.sessionId
                },
                createFormRules:{
                    title: [
                        { required: true, message: '请输入文件名称', trigger: 'blur' }
                    ],
                    detail: [
                        { required: true, message: '请输入文件描述', trigger: 'blur' },
                    ]
                },
                fileList: []
            }
        },
        methods:{
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handleSuccess(e) {
                if (e.status == 'ok') {
                    this.$message({
                        message: '创建成功',
                        type: 'success'
                    })
                } else {
                    this.$message({
                        message: e.data,
                        type: 'error'
                    })
                }
            },
            handleError(e){
                console.log(e)
            },
            handlePreview(file) {
                console.log(file);
            },
            CreatUser(formName) {
                let that=this
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$refs.upload.submit();
                        // creat item
                        // let temp={
                        //     title:that.createForm.title,
                        //     detail:that.createForm.content
                        // }
                        // CreateFile(temp).then(function (e) {
                        //     if(e.status=='ok'){
                        //         that.$message({
                        //             message:'创建成功',
                        //             type:'success'
                        //         })
                        //     }else {
                        //         that.$message({
                        //             message:e.data,
                        //             type:'error'
                        //         })
                        //     }
                        // }).catch(function (e) {
                        //     console.log(e)
                        // })
                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>

</style>



